import { CommonModule } from '@angular/common';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { NgModule, CUSTOM_ELEMENTS_SCHEMA, NO_ERRORS_SCHEMA } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';

import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { ChangePasswordComponent } from './auth/change-password/change-password.component';
import { MaterialModule } from './core-module/material.module';
import { InterceptorService } from './@core/services/authentication-interceptor.service';
import { ForgetPasswordComponent } from './auth/forget-password/forget-password.component';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { JwtHelperService, JWT_OPTIONS } from '@auth0/angular-jwt';
import { AuthGuard } from './@core/guards/auth.guard';
import { CacheRouteReuseStrategy } from './@core/utils/cache-route-reuse.strategy';
import { RouteReuseStrategy } from '@angular/router';
import { AccessGuard } from './@core/guards/access.guard';


@NgModule({
    imports: [
        CommonModule,
        BrowserModule,
        BrowserAnimationsModule,
        HttpClientModule,
        AppRoutingModule,
        MaterialModule,
        FormsModule,
        ReactiveFormsModule,
    ],
    declarations: [AppComponent, ChangePasswordComponent, ForgetPasswordComponent],
    schemas: [
        CUSTOM_ELEMENTS_SCHEMA,
        NO_ERRORS_SCHEMA
    ],
    providers: [
        { provide: HTTP_INTERCEPTORS, useClass: InterceptorService, multi: true },
        { provide: JWT_OPTIONS, useValue: JWT_OPTIONS },
        { provide: RouteReuseStrategy, useClass: CacheRouteReuseStrategy },
        JwtHelperService,
        AccessGuard,
        AuthGuard],
    bootstrap: [AppComponent]
})
export class AppModule {}
