import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ChangePasswordComponent } from './auth/change-password/change-password.component';
import { ForgetPasswordComponent } from './auth/forget-password/forget-password.component';
 
const routes: Routes = [    
    { path: 'howd', loadChildren: () => import('./layout/layout.module').then(m => m.LayoutModule), },
    {path: 'howd-admin', loadChildren: () => import('./admin/admin.module').then(m => m.AdminModule)},
    // { path: '', loadChildren: () => import('./auth/home/home.module').then(m => m.HomeModule) },
    { path: '', loadChildren: () => import('./auth/admin-login/admin-login.module').then(m => m.AdminLoginModule) },
    { path: 'signup', loadChildren: () => import('./auth/signup/signup.module').then(m => m.SignupModule) },
    { path: 'error', loadChildren: () => import('./auth/server-error/server-error.module').then(m => m.ServerErrorModule) },
    { path: 'access-denied', loadChildren: () => import('./access-denied/access-denied.module').then(m => m.AccessDeniedModule) },
    { path: 'not-found', loadChildren: () => import('./auth/not-found/not-found.module').then(m => m.NotFoundModule) },
    { path: 'change-password/:id', component: ChangePasswordComponent },
    { path: 'forget-password', component: ForgetPasswordComponent },
    // { path: 'mobile-telehealth', component: MobileTelehealthComponent },
    { path: 'changePassword', component: ChangePasswordComponent },
    { path: '**', redirectTo: 'not-found' }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
